html * {
  margin: 0;
  padding: 0;
}

hr {
  height: 3px;
  border: 0;
  background-color: #6e6d6b;
  opacity: 0.4;
}
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

header {
  padding: 10px 0;
}

.header_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header_logo {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.header_logo_text {
  font-size: 37px;
  color: #fb9c06;
  padding-left: 50px;
}

.rabbit {
  color: #6e6d6b;
}

// menu

.topmenu {
  list-style: none;
  display: flex;
  position: relative;
  text-align: center;
}

li {
  padding: 0 10px;
}

.submenu {
  position: absolute;
  display: none;
  list-style: none;
  background-color: #fff;
}

.topmenu li:hover .submenu {
  display: block;
}

.menu_link {
  text-decoration: none;
  font-size: 22px;
  color: #9e9b9b;
}

.menu_link:hover {
  text-decoration: underline;
}

.menu_icon {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

// slider

.slider {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 400px;
  background: #fb9c06 url(../img/big-orange-bg.png);
  border-top: 4px solid #fb9c06;
  border-bottom: 4px solid #fb9c06;
}

.slider_title_bg {
  margin: 0 0 30px 30px;
  width: 470px;
  color: #fb9c06;
  background-color: #6e6d6b;
}

.slider_title {
  padding-left: 20px;
  font-size: 37px;
}

.slider_subtitle_bg {
  width: 370px;
  margin-left: 40px;
  background-color: #fb9c06;
}

.slider_subtitle {
  padding-left: 25px;
  font-size: 37px;
  color: #6e6d6b;
}

// textarea

.textarea {
  display: flex;
  margin: 35px 0;
}

.textarea_item {
  padding: 0 20px;
  width: 25%;
  font-size: 15px;
}

.read_more {
  text-align: right;
}

.read_more_btn {
  text-decoration: none;
  color: #fb9c06;
  font-size: 17px;
  font-style: italic;
}

// title

.title {
  margin: 30px 0 0 0;
  text-align: center;
  font-size: 40px;
  font-weight: 400;
  color: #ff9c00;
  text-decoration: underline;
}

// meet the team

.meet_the_team {
  display: flex;
}

.meet_item {
  width: 33.33%;
  text-align: center;
  padding: 20px 20px;
}

.meet_item_title {
  font-size: 22px;
  color: #6e6d6b;
  margin: 50px 0 30px 0;
  font-weight: 400;
}

.meet_description {
  color: #ff9c00;
}

.meet_item_img {
  margin-bottom: 15px;
}

.meet_item_text {
  text-align: left;
  font-size: 15px;
  padding: 0 55px;
}

// contact form

form {
  display: flex;
  flex-direction: column;
  margin: 30px 0;
}

label {
  color: #ff9c00;
}
input {
  width: 1070px;
  float: right;
  margin-bottom: 10px;
}
textarea {
  width: 1070px;
  float: right;
}
// footer

footer {
  padding-top: 20px;
  text-align: center;
  background-color: #ff9c00;
  height: 50px;
  font-size: 23px;
}
